"use client";

import { useEffect, useRef, useState } from "react";
import Link from "next/link";
import Image from "next/image";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Navbar from "./navbar";

const WorkShowcase = () => {
  const containerRef = useRef(null);
  const sectionsRef = useRef([]);
  const [isListView, setIsListView] = useState(false);

  // Sample projects data
  const projects = [
    {
      id: 1,
      name: "Rivian",
      image: "/placeholder.svg?height=800&width=1200",
      url: "/work/rivian",
      number: "01",
    },
    {
      id: 2,
      name: "Du Chateau",
      image: "/placeholder.svg?height=800&width=1200",
      url: "/work/du-chateau",
      number: "02",
    },
    {
      id: 3,
      name: "Oura Ring",
      image: "/placeholder.svg?height=800&width=1200",
      url: "/work/oura-ring",
      number: "03",
    },
    {
      id: 4,
      name: "Tesla",
      image: "/placeholder.svg?height=800&width=1200",
      url: "/work/tesla",
      number: "04",
    },
  ];

  useEffect(() => {
    // Register ScrollTrigger plugin
    gsap.registerPlugin(ScrollTrigger);

    // Get all section elements
    const sections = sectionsRef.current;
    const container = containerRef.current;

    if (!container || sections.length === 0) return;

    // Set initial height for all sections
    gsap.set(sections, { height: "100vh" });

    // Create a timeline for each section
    sections.forEach((section, index) => {
      // Skip the first section as it should remain full height initially
      if (index === 0) return;

      // Create scroll trigger for parallax effect
      const tl = gsap.timeline({
        scrollTrigger: {
          trigger: section,
          start: "top bottom", // Start when the top of the section hits the bottom of the viewport
          end: "top top", // End when the top of the section hits the top of the viewport
          scrub: 0.5, // Smooth scrubbing effect
        },
      });

      // Animate previous sections to become shorter
      for (let i = 0; i < index; i++) {
        tl.to(
          sections[i],
          {
            height: "30vh", // Shrink to 30% of viewport height
            ease: "power2.inOut",
          },
          0
        );
      }
    });

    // Clean up ScrollTrigger on component unmount
    return () => {
      ScrollTrigger.getAll().forEach((trigger) => trigger.kill());
    };
  }, []);

  const toggleView = () => {
    setIsListView(!isListView);

    // Animate transition between views
    if (!isListView) {
      // Animate to list view
      gsap.to(sectionsRef.current, {
        height: "30vh",
        duration: 0.8,
        ease: "power2.inOut",
        stagger: 0.1,
      });
    } else {
      // Reset to default view
      gsap.to(sectionsRef.current, {
        height: "100vh",
        duration: 0.8,
        ease: "power2.inOut",
      });

      // Re-initialize scroll triggers
      setTimeout(() => {
        ScrollTrigger.refresh();
      }, 1000);
    }
  };

  return (
    <div
      ref={containerRef}
      className="relative w-full bg-black text-white overflow-hidden"
    >
      {/* Header */}
      <Navbar />
      {/* Project Sections */}
      {projects.map((project, index) => (
        <section
          key={project.id}
          ref={(el) => (sectionsRef.current[index] = el)}
          className="relative w-full h-screen overflow-hidden transition-all duration-500"
        >
          <div className="absolute inset-0 w-full h-full">
            <Image
              src={project.image || "/placeholder.svg"}
              alt={project.name}
              fill
              priority={index === 0}
              className="object-cover"
            />
            <div className="absolute inset-0 bg-black/30" />
          </div>

          <div className="absolute inset-0 flex items-center justify-center">
            <Link href={project.url} className="group">
              <h2 className="text-6xl md:text-7xl lg:text-8xl font-light tracking-tight border-b border-white/30 pb-2 transition-all duration-300 group-hover:border-white">
                {project.name}
              </h2>
            </Link>
          </div>

          <div className="absolute bottom-8 right-10 text-xl font-light">
            {project.number}
          </div>
        </section>
      ))}

      {/* List View Toggle Button */}
      <button
        onClick={toggleView}
        className="fixed bottom-8 left-10 z-50 flex items-center gap-2 text-white/80 hover:text-white transition-colors"
      >
        <span className="w-6 h-6 flex items-center justify-center border border-white/30 rounded-full">
          :
        </span>
        <span>List view</span>
      </button>
    </div>
  );
};

export default WorkShowcase;
